import React from 'react';


const CommentsList = ({ comments }) => (
    <>
    <h3>Comments</h3>
    <ul class="comments-list">
        {comments.slice(0).reverse().map((comment, key) => (
            <li>
                <div className="comment" key={key}>
                    <h4>{comment.username}</h4>
                    <p>{comment.text}</p>
                </div>
            </li>
            
        ))}
    </ul>
    </>
);


export default CommentsList;